<h1 id="dependency-injection-in-node-js" tabindex="-1"><a class="header-anchor" href="#dependency-injection-in-node-js" aria-hidden="true">#</a> Dependency Injection in Node.js</h1>
<p>When writing tests, we optimally want to test one function at a time in our application, but often it's more complicated than that. For example, we have one function calling another function, which then in turn calls another function, and so on. How can we test just the outer most function without worrying about errors in the inner most functions? <em>Dependency injection</em> to the rescue, which is not only useful for testing, but also for fast prototyping and simulation.</p>
